  text-align: center;
  background: url(assets/accuhit-logo.png) whitesmoke no-repeat center center;
  background-size: contain;
}

.content {
  width: 100%;
  top:40px;
  height:350px;
  background-color: lightgray;
  position: absolute;
  overflow: auto;
}

