  padding: 5px;
  list-style: none;
}

.balloon {
  margin: 10px 0;
  display: flex;
  align-items: flex-start;
}
.balloon-r {
  margin-right: 5px;
  justify-content: flex-end;
}

.img-circle {
  width: 35px;
  height: 35px;
  margin: 0 5px;
  border-radius: 25px;
  background-color: white;
}

.talk {
  max-width: 500px;
  padding: 10px;
