  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
  position:absolute;
}

.msg-input {
  width: 70%;
  margin-right: 10px;
  padding: 5px 15px;
  border: none;
  border-bottom: 1px solid gray;
  border-radius: 25px;
  background-color: whitesmoke;
}

.btn-submit {
  width: 25px;
  height: 25px;
  padding: 6px;
  border: none;
  border-radius: 5px;
  background: url(assets/send.png) no-repeat center center;
  background-size: contain;
  cursor: pointer;
}
</style>
